/*
--------------------------------------------------------------
                        Menu style
--------------------------------------------------------------
*/

.DashTab,
a:link {
    color: #ffffff;
    font-size: 2rem;
    text-decoration: none;
}

body {
    height: 100%;
    text-align: left;
    z-index: -1;
    /* position: relative; */
}

.TabClassifier {
    font-size: 2.5rem;
}

/*
--------------------------------------------------------------
                            Font styles
--------------------------------------------------------------
*/

h1 {
    font-size: 3.5rem;
}

p {
    font-size: 2rem;
}

/*
--------------------------------------------------------------
                        Page styles
--------------------------------------------------------------
*/

.AdminContentPageStyle {
    padding: 5rem;
}

.ChartsPageStyle {
    bottom: 10%;
    text-align: center;
}

.AboutPageStyle {
    padding: 20rem;
    margin-top: 2rem;
}

/*
---------------------------------------------------
                       Team tab style
---------------------------------------------------
*/

.TeammateStyle {
    background-color: rgb(109, 104, 104);
    border-radius: 50%;
    margin: 0.5rem;
    text-align: justify;
}

.TeamPicStyle {
    font-size: 10rem;
    margin: 1rem;
    padding: 2rem;
}

.test {
    text-align: center;
    backdrop-filter: blur(16px) saturate(180%);
    -webkit-backdrop-filter: blur(16px) saturate(180%);
    background-color: rgba(2105, 255, 255, 0.75);
    border-radius: 12px;
    border: 3.5px solid rgba(29, 38, 50, 0.3);
    padding: 2rem;
    margin: 0.1;
}

.TeamFontStyle {
    font-family: 'Estonia', cursive;
}

.TeamHandle {
    background-color: #000000eb;
    border-radius: 500px;
    color: #ffffff;
    padding: 1rem;
}

.editButton {
    font-size: 2rem;
    background-color: transparent;
    color: #fff;
    cursor: pointer;
    margin: 0.5rem;
    padding: 0.2rem;
    border: 0.00001px thin #000000eb;
}

/*
---------------------------------------------------
                Charts tab style
---------------------------------------------------
*/

.DataMetricsStyle {
    margin-left: 10rem;
}

.PanelLeft1,
.PanelLeft2,
.PanelRight1,
.PanelRight2 {
    background-color: rgba(156, 111, 156, 0.705);
    text-align: left;
    color: #ffffff;
    padding: 0rem 20rem 0rem 20rem;
    margin: 0.2rem;
}

.PanelLeft1 {
    background-color: rgba(65, 160, 238, 0.705);
}
.PanelLeft2 {
    background-color: rgba(238, 65, 94, 0.705);
}
.PanelRight1 {
    background-color: rgba(18, 119, 5, 0.705);
}

.PanelRight2 {
    background-color: rgba(224, 65, 238, 0.705);
}

#mainMenu {
    width: auto;
    display: block;
    text-align: center;
    line-height: 1.2;
}
#mainMenu ul {
    width: auto;
    display: block;
    font-size: 0;
    text-align: center;
    color: #fffdfd;
    background-color: #000000eb;
    border: transparent;
    margin: 0;
    padding: 0;
    list-style: none;
    position: relative;
    z-index: 999999990;
    border-radius: 3px;
}

#mainMenu li {
    display: inline-block;
    position: relative;
    font-size: 0;
    margin: 0;
    padding: 0;
}

/*
--------------------------------------------------------------
                            Top level items
--------------------------------------------------------------
*/

#mainMenu > ul > li > span,
#mainMenu > ul > li > a {
    font-size: 22px;
    color: inherit;
    text-decoration: none;
    padding: 20px 20px;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 2px;
    display: block;
    position: relative;
    transition: all 0.3s;
}
#mainMenu li:hover > span,
#mainMenu li:hover > a {
    color: #000000eb;
    background-color: #ffffff;
}

/*
--------------------------------------------------------------
                           Sub level items
--------------------------------------------------------------
*/

#mainMenu .dropdown {
    text-align: left;
    left: 0;
    font-family: inherit;
    color: #ffffff;
    background-color: #000000eb;
    border: none;
    position: absolute;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    display: none;
    opacity: 0;
    cursor: default;
}

#mainMenu .dropdown li {
    display: block;
}

#mainMenu .dropdown li > span,
#mainMenu .dropdown li > a,
#mainMenu .clm a,
#mainMenu .clm h3 {
    font-size: 16px;
    font-weight: 400;
    font-family: inherit;
    margin: 0;
    padding: 8px 20px;
    display: block;
    color: inherit;
    text-decoration: none;
}
#mainMenu .clm h3 {
    font-size: 20px;
    font-weight: 700;
}
#mainMenu .sub-item {
    background-color: #ffffff;
    position: relative;
    transition: all 0.3s;
}

#mainMenu .dropdown li:hover > span,
#mainMenu .dropdown li:hover > a {
    color: #000000;
    background-color: #eeeeee;
}

#mainMenu .clm a:hover {
    color: #000000;
    transition: color 0.2s;
}

#mainMenu .dropdown.right0 {
    left: auto;
    right: 0;
}
#mainMenu .dropdown li > .dropdown.right0 {
    left: auto;
    right: 100%;
}

#mainMenu li.full-width {
    position: static;
}
#mainMenu li.full-width .dropdown {
    width: 100%;
    left: 0;
    box-sizing: border-box;
}

#mainMenu li:hover > .dropdown {
    display: block;
    opacity: 1;
    z-index: 1;
}

#mainMenu .dropdown li > .dropdown {
    left: 100%;
    right: auto;
    top: 0;
}

#mainMenu ul.dropdown {
    min-width: 240px;
}

#mainMenu div.dropdown {
    text-align: center;
}

#mainMenu .clm {
    text-align: left;
    margin: 20px;
    vertical-align: top;
    width: auto;
    min-width: 240px;
    display: inline-block;
    *display: inline;
    *zoom: 1;
}

#mainMenu .clm a:hover {
    color: #000000;
    text-decoration: underline;
}

#mainMenu .arrow {
    color: inherit;
    border-style: solid;
    border-width: 2px 2px 0 0;
    padding: 5px;
    transform: rotate(135deg);
    margin-top: -10px;
    margin-left: 5px;
    position: relative;
    display: inline-block;
    width: 0;
    height: 0;
    vertical-align: middle;
    overflow: hidden;
}

#mainMenu .dropdown .arrow {
    transform: rotate(45deg);
    top: 50%;
    margin-top: -7px;
    position: absolute;
    left: auto;
    right: 20px;
}

@keyframes topItemAnimation {
    from {
        opacity: 0;
        transform: translate3d(-16%, 0, 0);
    }
    to {
        opacity: 1;
        transform: translate3d(0, 0, 0);
    }
}
#mainMenu li a {
    animation: none;
}
#mainMenu.active li a {
    animation: topItemAnimation 0.5s ease 0.5s backwards;
}
#mainMenu.active li:nth-of-type(1) a {
    animation: topItemAnimation 0.5s ease 0.08333333333333333s backwards;
}
#mainMenu.active li:nth-of-type(2) a {
    animation: topItemAnimation 0.5s ease 0.16666666666666666s backwards;
}
#mainMenu.active li:nth-of-type(3) a {
    animation: topItemAnimation 0.5s ease 0.25s backwards;
}
#mainMenu.active li:nth-of-type(4) a {
    animation: topItemAnimation 0.5s ease 0.3333333333333333s backwards;
}
#mainMenu.active li:nth-of-type(5) a {
    animation: topItemAnimation 0.5s ease 0.4166666666666667s backwards;
}
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    #mainMenu.active li a {
        animation: none !important;
    }
}

body {
    margin: 0;
    background: #ffffff;
}
#menuDiv {
    text-align: center;
}
